import rateLimit from 'express-rate-limit';
import slowDown from 'express-slow-down';
// Redis store for distributed rate limiting (optional)
// import RedisStore from 'rate-limit-redis';
// import Redis from 'ioredis';
// const redis = new Redis(process.env.REDIS_URL || 'redis://localhost:6379');
// General API rate limit
export const generalRateLimit = rateLimit({
    windowMs: 15 * 60 * 1000,
    max: 100,
    message: {
        success: false,
        error: 'Too many requests from this IP, please try again later.',
        retryAfter: 15 * 60 // seconds
    },
    standardHeaders: true,
    legacyHeaders: false,
    // store: new RedisStore({
    //   sendCommand: (...args: string[]) => redis.call(...args),
    // }),
    handler: (req, res)=>{
        res.status(429).json({
            success: false,
            error: 'Rate limit exceeded',
            limit: 100,
            windowMs: 15 * 60 * 1000,
            retryAfter: Math.round(15 * 60),
            ip: req.ip,
            path: req.path
        });
    }
});
// Strict rate limit for authentication endpoints
export const authRateLimit = rateLimit({
    windowMs: 15 * 60 * 1000,
    max: 5,
    message: {
        success: false,
        error: 'Too many authentication attempts, please try again later.',
        retryAfter: 15 * 60
    },
    standardHeaders: true,
    legacyHeaders: false,
    skipSuccessfulRequests: true,
    handler: (req, res)=>{
        res.status(429).json({
            success: false,
            error: 'Authentication rate limit exceeded',
            limit: 5,
            windowMs: 15 * 60 * 1000,
            retryAfter: Math.round(15 * 60),
            ip: req.ip,
            path: req.path
        });
    }
});
// API rate limit for high-frequency endpoints
export const apiRateLimit = rateLimit({
    windowMs: 1 * 60 * 1000,
    max: 50,
    message: {
        success: false,
        error: 'API rate limit exceeded',
        retryAfter: 60
    },
    standardHeaders: true,
    legacyHeaders: false
});
// Test execution rate limit (more permissive for testing)
export const testExecutionRateLimit = rateLimit({
    windowMs: 5 * 60 * 1000,
    max: 20,
    message: {
        success: false,
        error: 'Test execution rate limit exceeded',
        retryAfter: 5 * 60
    },
    standardHeaders: true,
    legacyHeaders: false
});
// Slow down middleware for additional protection
export const speedLimiter = slowDown({
    windowMs: 15 * 60 * 1000,
    delayAfter: 50,
    delayMs: (used)=>{
        return (used - 50) * 500; // Add 500ms delay for each request after delayAfter
    },
    maxDelayMs: 10000,
    skipFailedRequests: false,
    skipSuccessfulRequests: false
});
// Heavy operation rate limit (for resource-intensive operations)
export const heavyOperationRateLimit = rateLimit({
    windowMs: 60 * 60 * 1000,
    max: 10,
    message: {
        success: false,
        error: 'Heavy operation rate limit exceeded',
        retryAfter: 60 * 60
    },
    standardHeaders: true,
    legacyHeaders: false
});
// File upload rate limit
export const uploadRateLimit = rateLimit({
    windowMs: 60 * 60 * 1000,
    max: 20,
    message: {
        success: false,
        error: 'Upload rate limit exceeded',
        retryAfter: 60 * 60
    },
    standardHeaders: true,
    legacyHeaders: false
});
